iner .circle{transition:0.2s;display:flex;align-items:center;justify-content:center;border-radius:50%;background-color:white}.container .circle.small{width:70px;height:70px}.container .face{z-index:5}.container:hover .face,.container.force-hover .face{transform:scale(1.1)}@media screen and (min-height: 750px){.container{width:500px;height:500px}.container .item-container{position:absolute;top:40%;left:50%;transform:translate(-50%, -50%)}.container .circle{width:200px;height:200px}.container .face img{transform:scale(1.4)}.container :nth-child(2) .item{transition-delay:0.025s}.container :nth-child(3) .item{transition-delay:0.05s}.container :nth-child(4) .item{transition-delay:0.075s}.container :nth-child(5) .item{transition-delay:0.1s}.container :nth-child(6) .item{transition-delay:0.125s}.container :nth-child(7) .item{transition-delay:0.15s}.container :nth-child(8) .item{transition-delay:0.175s}.container :nth-child(9) .item{transition-delay:0.2s}.container:hover :nth-child(2) .item,.container.force-hover :nth-child(2) .item{transform:translate(0%, -220%)}.container:hover :nth-child(3) .item,.container.force-hover :nth-child(3) .item{transform:translate(155.56349%, -155.56349%)}.container:hover :nth-child(4) .item,.container.force-hover :nth-child(4) .item{transform:translate(220.0%, 0%)}.container:hover :nth-child(5) .item,.container.force-hover :nth-child(5) .item{transform:translate(155.56349%, 155.56349%)}.container:hover :nth-child(6) .item,.container.force-hover :nth-child(6) .item{transform:translate(-.00012%, 219.99997%)}.container:hover :nth-child(7) .item,.container.force-hover :nth-child(7) .item{transform:translate(-155.56706%, 155.56264%)}.container:hover :nth-child(8) .item,.container.force-hover :nth-child(8) .item{transform:translate(-220.06627%, -.0182%)}.container:hover :nth-child(9) .item,.container.force-hover :nth-child(9) .item{transform:translate(-156.43219%, -155.8323%)}}@media screen and (max-height: 750px){.container{width:400px;height:300p