@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400&display=swap');

$light-color-1: #77c4db;
$light-color-2: #9ccfdd;
$light-color-3: #b2d5de;

$dark-color-1: #1f3339;
$dark-color-2: #2a393e;
$dark-color-3: #353e40;

$numElems: 8;

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;

  background-color: $light-color-1;
  background-image: linear-gradient(30deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(150deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(30deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(150deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(60deg, $light-color-3 25%, transparent 25.5%, transparent 75%, $light-color-3 75%, $light-color-3),
  linear-gradient(60deg, $light-color-3 25%, transparent 25.5%, transparent 75%, $light-color-3 75%, $light-color-3);
  background-size: 40px 70px;
  background-position: 0 0, 0 0, 20px 35px, 20px 35px, 0 0, 20px 35px;
}

@function pow($number, $exp) {
  $value: 1;
  @if $exp > 0 {
    @for $i from 1 through $exp {
      $value: $value * $number;
    }
  } @else if $exp < 0 {
    @for $i from 1 through -$exp {
      $value: $value / $number;
    }
  }
  @return $value;
}

@function fact($number) {
  $value: 1;
  @if $number > 0 {
    @for $i from 1 through $number {
      $value: $value * $i;
    }
  }
  @return $value;
}

@function pi() {
  @return 3.14159265359;
}

@function rad($angle) {
  $unit: unit($angle);
  $unitless: $angle / ($angle * 0 + 1);
  // If the angle has 'deg' as unit, convert to radians.
  @if $unit == deg {
    $unitless: $unitless / 180 * pi();
  }
  @return $unitless;
}

@function sin($angle) {
  $sin: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $sin: $sin + pow(-1, $i) * pow($angle, (2 * $i + 1)) / fact(2 * $i + 1);
  }
  @return $sin;
}

@function cos($angle) {
  $cos: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $cos: $cos + pow(-1, $i) * pow($angle, 2 * $i) / fact(2 * $i);
  }
  @return $cos;
}

.app {
  width: 100vw;
  height: 80vh;

  overflow: hidden;

  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  position: fixed;
  top: 2%;
  font-size: 2em;
  font-weight: bold;
  background-color: white;
  padding: 15px;
  border-radius: 15px;
  text-align: center;
  width: 40%;
  min-width: 250px;
}

.description {
  position: fixed;
  bottom: 2%;
  left: 50%;
  transform: translate(-50%, 0);

  width: 40%;
  min-width: 400px;
  height: 200px;

  background-color: white;

  border-radius: 20px;
  padding: 15px;

  transition: 0.2s;

  .header {
    padding-left: 15px;
    font-weight: bold;
    font-size: 1.5em;
    border: none;
    border-bottom: 1px solid black;
    margin-bottom: 20px;
  }

  .body {

  }

  .education-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

    .date {
      padding-left: 10px;
    }
    .name {
      font-weight: bold;
    }
  }
}

.warning {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: white;

  width: 250px;
  height: 50px;

  padding: 10px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;

  visibility: hidden;
  font-weight: bold;

}

.container {
  display: flex;
  align-items: center;
  justify-content: center;

  .item-container {
    font-size: 2em;
    z-index: 2;

    &:hover, &.force-hover {
      .item {
        background-color: #61beef;
      }
    }
  }

  .circle {
    transition: 0.2s;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;

    background-color: white;

    &.small {
      width: 70px;
      height: 70px;
    }
  }

  .face {
    z-index: 5;
  }

  &:hover, &.force-hover {
    .face {
      transform: scale(1.1);
    }
  }
}

@media screen and (min-height: 750px) {
  .container {
    width: 500px;
    height: 500px;

    .item-container {
      position: absolute;
      top: 40%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    .circle {
      width: 200px;
      height: 200px;
    }

    .face img {
      transform: scale(1.4);
    }

    @for $i from 2 through ($numElems + 1) {
      :nth-child(#{$i}) {
        .item {
          transition-delay: 0.025s * ($i - 1);
        }
      }
    }

    &:hover, &.force-hover {
      @for $i from 2 through ($numElems + 1) {
        :nth-child(#{$i}) {
          $degPerElem: 360deg / $numElems;
          $angle: (($i - 2) * $degPerElem)+90deg;
          $radi: -220%;
          $xval: cos($angle)*$radi;
          $yval: sin($angle)*$radi;

          .item {
            transform: translate($xval, $yval);
          }
        }
      }
    }
  }
}

@media screen and (max-height: 750px) {

  .container {
    width: 400px;
    height: 300px;
    flex-wrap: wrap;

    div {
      margin: 5px;
    }

    .item-container {
      position: relative;
    }

    .circle {
      width: 70px;
      height: 70px;
    }

    .face img {
      width: 70px;
      height: 70px;
      transform: scale(1);
    }
  }
  //.container, .description {
